<template>
    <div>
        <div class="notice-box" v-for="item in state.noticeList" :key="item.id" @click="clickDetail(item)">
            <div class="notice-title">{{ item.title }}</div>
            <div class="notice-time">{{ item.created_at }}</div>
            <div class="notice-line"></div>
        </div>
    </div>
</template>

<script setup>

import { reactive, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { getNotice } from '@/utils/api'
import { Toast } from 'vant';

const router = useRouter()
const state = reactive({
    noticeList: []
})

onMounted(async () => {
    Toast.loading()
    const { data } = await getNotice()
    state.noticeList = data
})

const clickDetail = ((item) => {
    router.push({ path: 'noticeDetail', query: { title: item.title, time: item.created_at, content: item.content } })
})

</script>

<style lang="less" scoped>
@import '@/common/base.less';

.notice-box {
    text-align: left;

    .notice-title {
        margin: 15px 15px 0 15px;
        .scw(@titleColorOne, 18px, 500);
        font-family: PingFangSC-Medium, PingFang SC;
    }

    .notice-time {
        margin: 2px 15px 0 15px;
        .scw(@titleColorTwo, 14px, 400);
    }

    .notice-line {
        margin-top: 15px;
        height: 1px;
        background: #2E3138;
    }
}
</style>